@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,400;0,700;1,200;1,400;1,700&display=swap');

* {
  margin: 0;
  padding: 0;
}

@mixin for-phone-only {
  @media (max-width: 599px) { @content; }
}

body {
  font-size: 14px;
  background-color: #121020;
  font-family: 'Poppins', sans-serif;
  font-weight: 400; // 200, 400, 700

}

::selection {
  background-color: #EA456E;
  color: #fff;
}

.container {
  width: 1200px;
  margin: auto;
  
  @include for-phone-only {
    width: auto;
    overflow: hidden;  
  }
}

header {
  padding: 60px 0;
  display: flex;
  justify-content: flex-end;

  @include for-phone-only {
    padding: 30px 0;
    // padding-bottom: 0px;
    background-color: #121020;
    position: fixed;
    width: 100%;
    z-index: 2;
  }
}

.p {
  @include for-phone-only {
    padding-top: 100px;
  }
}

.arrow-tilt {
  transform: rotate(-45deg);
  position: relative;
  top: 2px;
  margin-left: 3px;
}

nav {
  
  ul {
    float: right;
    display: flex;

    @include for-phone-only {
      float: auto;
    }
    
    li {
      text-transform: uppercase;
      list-style: none;
      color: #fff;
      margin-right: 30px;
      font-weight: 400;

      @include for-phone-only {
        margin-right: 10px;
      }

      a {
        text-decoration: none;
        color: #fff;
        transition: .3s;
        cursor: pointer;
        padding: 15px;
        letter-spacing: 0.5px;
        
        &.active {
          color: #EA456E;
          // font-weight: 700;
        }
      
        &:hover {
          color: #EA456E;
        }
      }

      &:last-child {
        margin-right: 0px;
      }
    }
  }
}

.hero-section {
  display: flex;
  margin-top: 100px;

  @include for-phone-only {
    flex-direction: column-reverse;
    margin-top: 0px;
  }
  
  .content {
     width: 660px;
     color: #fff;

     @include for-phone-only {
       width: 100%;
     }

     .line-wrap {
      overflow: hidden;
      height: 85px;

      @include for-phone-only {
        height: auto;
      }

     }

     h1 {
      font-size: 56px;
      font-weight: 700;

      @include for-phone-only {
        font-size: 36px;
        margin-top: 30px;
        text-align: center;
      }
    }

    .paragraph-content {
      font-size: 36px;
      font-weight: 200;
      line-height: 61px;
      margin-bottom: 20px;

      @include for-phone-only {
        font-size: 18px;
        line-height: 18px;
        padding: 20px;
        margin-bottom: 0;
      }

      .line-wrap-p {
        overflow: hidden;
        height: 60px;
        
        @include for-phone-only {
          height: 20px;
          margin-bottom: 10px;
        }

        span {
          display: block;
          position: relative;
        }
      }
    }

    .line-wrap-link {
      overflow: hidden;
      height: 57px;

      @include for-phone-only {
        height: 20px;
        font-size: 14px;
        padding: 0 20px;
      }
    }

    a {
      text-decoration: none;
      display: block;
      color: #EA456E;
      font-size: 18px;
      padding: 15px 0;
      transition: 0.3s;

      @include for-phone-only {
        padding: 0;
        font-size: 14px;
      }

      &:hover {
        color: #CA0A3A;

        .icon {
          left: 10px;
        }
      }

      .icon {
        position: relative;
        left: 4px;
        top: 0px;
        width: 20px;
        height: 20px;
        display: inline-block;
        overflow: hidden;
        transition: 0.3s;

        @include for-phone-only {
          top: 6px;
        }
      }
    }
  }

  .image-container {
    display: flex;
    flex-grow: 1;
    justify-content: center;
    position: relative;
    
    .image {
      width: 372px;
      height: 372px;
      border-radius: 50%;
      background-color: #fff;
      position: relative;

      @include for-phone-only {
        width: 200px;
        height: 200px;
      }

      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
      }
      
      &:before {
        content: '';
        position: absolute;
        width: 332px;
        height: 332px;
        left: 10px;
        top: 10px;
        background-color: #FF0073;
        border-radius: 100%;
        box-shadow: 27px 25px 37px 15px #ff0073;
        z-index: -1;

        @include for-phone-only {
          display: none;
        }
      }
    }
  }
}

@keyframes glow {
  from {
    box-shadow: 0 0 10px 2px  #FF0073;
  }
  to {
    box-shadow: 0 0 70px 5px  #FF0073;
  }
}

.brands-container {
  display: flex;
  justify-content: center;
  margin-top: 165px;

  @include for-phone-only {
    flex-wrap: wrap;
    margin-top: 80px;
  }

  .brands {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @include for-phone-only {
      padding: 0 20px;
    }
  }
  
  h3 {
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: #5F5D68;
    margin-bottom: 50px;

    @include for-phone-only {
      margin-bottom: 25px;
    }
  }

  ul.logos {
    display: flex;
    list-style: none;

    @include for-phone-only {
      flex-wrap: wrap;
      justify-content: center;
    }
    
    li {
      margin-right: 42px;
      transition: 0.3s;

      @include for-phone-only {
        margin-right: 0px;
        gap: 20px;
      }

      &:hover {
        transform: scale(1.1);
      }

      &:last-child {
        margin-right: 0;
      }
    }
  }
}

.work-container {
  margin-top: 165px;

  @include for-phone-only {
    margin-top: 60px;
  }

  h3 {
    font-size: 56px;
    font-weight: 700;
    color: #fff;
    margin-bottom: 64px;

    @include for-phone-only {
      font-size: 36px;
      text-align: center;
      margin-bottom: 10px;
    }
  }

  ul {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    gap: 8%;

    @include for-phone-only {
      gap: 20px;
      flex-direction: column;
      flex-wrap: unset;
    }

    li {

      a {
        color: inherit;
        display: block;
        text-decoration: none;
        transition: 0.3s;

        * {
          transition: 0.3s;
        }

        &:hover {
          transform: scale(1.01);
          
          .image img {
            // transform: scale(1.05);
          }

          span.link {
            color: #CA0A3A;
          }

          .icon {
            left: 10px;
          }
        }
      }

      width: 46%;
      display: block;
      // padding-right: 20px;
      box-sizing: border-box;
      margin-bottom: 65px;

      @include for-phone-only {
        width: 100%;
        padding: 20px;
        margin-bottom: 0px;
      }

    }

    h4 {
      font-size: 36px;
      font-weight: 700;
      color: #fff;
      margin-top: 14px;

      @include for-phone-only {
        font-size: 24px;
        margin-bottom: 5px;
      }
    } 

    .image {
      width: 100%;

      img { 
        width: 100%;
      }
    }

    p {
      color: #fff;
      font-size: 18px;
      
      @include for-phone-only {
        font-size: 16px;
      }
    }

    span.link {
      text-decoration: none;
      display: inline-block;
      color: #EA456E;
      font-size: 18px;
      padding: 15px 0;

      @include for-phone-only {
        font-size: 14px;
      }

      .icon {
        position: relative;
        left: 4px;
        top: 3px;
        width: 20px;
        height: 20px;
        display: inline-block;
        overflow: hidden;

        @include for-phone-only {
          top: 6px;
        }
      }
    }
  }
}

.testimonial-container {
  margin-top: 100px;
  padding: 100px 0;
  background-color: #474161;

  @include for-phone-only {
    margin-top: 40px;
    padding: 50px 0;
  }

  h3 {
    font-size: 56px;
    font-weight: 700;
    color: #fff;
    margin-bottom: 64px;

    @include for-phone-only {
      font-size: 36px;
      margin-bottom: 10px;
      text-align: center;
    }
  }

  ul.testimonials {
    list-style: none;
    width: 1300px;
    margin: auto;
    display: flex;
    gap: 2%;

    @include for-phone-only {
      width: 100%;
      flex-direction: column;
    }
    
    li {
      width: 31.33%;
      display: block;
      background-color: #585370;

      @include for-phone-only {
        width: auto;
        margin: 20px;
        margin-bottom: 10px;
      }
        

      .content {
        height: 350px;
        padding: 50px;
        color: #fff;
        font-size: 24px;
        display: flex;
        // align-items: center;
        justify-content: center;
        text-align: center;

        @include for-phone-only {
          font-size: 18px;
          height: 150px;
          padding: 30px;
          font-size: 16px;
        }
      }

      .person {
        font-size: 18px;
        color: #fff;

        @include for-phone-only {
          font-size: 14px;
        }

        .info {
          align-items: center;
          justify-content: center;
          padding-bottom: 50px;

          @include for-phone-only {
            padding-bottom: 30px;
          }
        }

        .image {
          width: 105px;
          // height: 90px;
          // background-color: #fff;
          margin: auto;
          margin-bottom: 10px;

          img {
            width: 100%;
          }
        }

        .info > p {
          text-align: center;
          // display: inline-block;
        }

        .name {
          font-weight: 700;
        }

        .position {
          color: #ABA9B7;
        }
      }
    }
  }
}

.footer {
  background-color: #000;
  color: #fff;
  padding: 125px 0;
  font-weight: 200;

  @include for-phone-only {
    padding: 40px 20px;
    text-align: center;
  }

  p {
    // margin-bottom: 10px;
    font-size: 18px;
  }

  h1 {
    font-size: 56px;
    font-weight: 700;
    margin: 0;
    padding: 0;
    // margin-bottom: 10px;
    @include for-phone-only {
      font-size: 24px;
    }
  }

  ul.social-links {
    list-style: none;
    margin-top: 30px;
    display: flex;
    gap: 20px;

    @include for-phone-only {
      justify-content: center;
      margin-top: 20px;
      margin-right: 20px;
    }

    li {
      list-style: none;
      transition: 0.3s;

      @include for-phone-only {
        // width: 20%;
      }

      svg {
        @include for-phone-only {
          width: 50px;
        }
      }

      &:hover {
        transform: translate(0px, -10px);
      }
    }
  }
}

